import { StorageService } from './../../../services/storage-service';
import { Session } from './../../../services/session';
import { Utility } from './../../../services/utility'; 
import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, Events, ViewController } from 'ionic-angular';
import { NativeService } from "../../../services/services";
import { ConfigProvider, ServerProvider, WidgetProvider } from "../../../providers/providers";
import { InAppBrowser } from '@ionic-native/in-app-browser';
/**
 * Generated class for the UpgradePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-upgrade',
  templateUrl: 'upgrade.html',
})
export class UpgradePage {
  currentVerionNo: string;
  lastestVerionNo: string;
  appDownloadPageUrl: string;
  lastVersionInfo: any;
  needUpdate: boolean = false;
  isDebug: Boolean = false;
  isIgnore:Boolean = false;
  canIgnore: boolean = true;
  constructor(public navCtrl: NavController, public modalCtrl: ModalController,
    public nativeService: NativeService,
    public configProvider: ConfigProvider,
    public events: Events,
    public iab: InAppBrowser,
    private serverProvider: ServerProvider,
    private widgetProvider: WidgetProvider,
    private session: Session,
    public viewCtrl: ViewController,
    public storageService: StorageService

  ) {
 
    this.init();

  }

  ionViewDidLeave(){
    this.configProvider.isShowUpgraderPage = false;
  }

  updateVersion() { 
    if (!this.appDownloadPageUrl) {
      this.appDownloadPageUrl = this.serverProvider.AppDownloadPage();
    }
    if (this.nativeService.isIos()) {
      this.widgetProvider.openOnSystem(this.appDownloadPageUrl)
    } else { 
      this.configProvider.getGlobalSettings().subscribe(res=>{ 
        if(res.errcode == 0){
          for(let item of res.result){
            if(item.name == 'AndroidUpgradeUrl'){ 
              let url = ""
              if(item.value && item.value.indexOf("?") > -1){
                url = item.value
              }else {
                url = item.value + '?Lang=' + this.session.language
              }
              window.open(url, '_system'); //'_system' 系统自带浏览器打开下载页
              break;
            }
          }
        }else {
          window.open(this.appDownloadPageUrl + '?Lang=' + this.session.language, '_system'); //'_system' 系统自带浏览器打开下载页
        }
        
      },err=>{
        window.open(this.appDownloadPageUrl + '?Lang=' + this.session.language, '_system'); //'_system' 系统自带浏览器打开下载页
      }) 
    }

  }

  init() {
    this.isDebug = this.serverProvider.isDebug;
    this.currentVerionNo = this.configProvider.currentVersionNo;
    this.lastestVerionNo = this.configProvider.latestVersionNo;
    this.lastVersionInfo = this.configProvider.lastVersionInfo;
    this.appDownloadPageUrl = this.configProvider.appDownloadPageUrl;

    for(let item of this.session.globalSetting) {
      if(item.name.toLowerCase() == 'minAppVersion'.toLowerCase()){
        this.canIgnore = !Utility.checkNewVersion(item.value, this.configProvider.currentVersionNo);
        break;
      }
    }
    
    this.needUpdate = Utility.checkNewVersion(this.lastestVerionNo, this.currentVerionNo);
  }

  ignored(){
    this.configProvider.canUpgrade = false;
    this.storageService.setItem("isIgnore" + this.lastestVerionNo, true);
    this.viewCtrl.dismiss();
    this.events.publish('upgrade:re-init');
  }

}



